/* FAQ item styling */
.faq-item {
    border: 1px solid #ddd !important; /* Consistent border */
    border-radius: 12px !important; /* Rounded corners */
    margin-bottom: 0 !important; /* No space between FAQ items */
    overflow: hidden !important; /* Contain overflow */
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) !important; /* Subtle shadow */
    transition: box-shadow 0.3s ease-in-out !important; /* Smooth transition */
}

/* FAQ question styling */
.faq-question {
    background-color: #f7f7f7 !important; /* Background color */
    color: #222 !important; /* Text color */
    font-size: 14px !important; /* Font size for questions */
    padding: 10px 12px !important; /* Padding for questions */
    cursor: pointer; /* Pointer cursor for interactivity */
    border-bottom: 1px solid #ddd !important; /* Bottom border */
    display: flex; /* Flex for alignment */
    justify-content: space-between; /* Space between text and toggle */
    align-items: center; /* Center alignment */
}

/* FAQ answer styling */
.faq-answer {
    padding: 10px 12px !important; /* Match padding with questions */
    background-color: #fff !important; /* Background color */
    color: rgb(51, 51, 51) !important; /* Text color */
    font-size: 14px !important; /* Font size for answers */
    line-height: 1.4 !important; /* Line height for readability */
    border-top: 1px solid #ddd !important; /* Top border for separation */
    margin: 0 !important; /* No margin for answers */
}

/* Answer paragraphs */
.faq-answer p {
    margin: 0 !important; /* No margin */
    padding: 0 !important; /* No padding */
    font-size: 14px !important; /* Ensure paragraph font size matches */
    color: rgb(51, 51, 51) !important; /* Match text color */
}

/* Styling for 'No FAQs found' message */
.faq-list p.no-faqs,
.faq-list p {  /* Adjust as necessary to match the actual class */
    color: rgb(51, 51, 51) !important; /* Set color to black */
}

/* Add separator line between FAQs */
.faq-item:not(:last-child) {
    border-bottom: 1px solid #ddd; /* Add line below each FAQ except the last */
}

/* Remove margin between items */
.faq-list > .faq-item + .faq-item {
    margin-top: 0 !important; /* Ensure no margin between items */
}
